    font-family: Manrope;
    font-size: 128px;
    font-style: normal;
    font-weight: 500;
    line-height: 90%; /* 115.2px */
    letter-spacing: -6.4px;
    text-transform: uppercase;
  }
  
  .wrapper{
    display: grid;
    grid-template-columns: 502px 1fr;
    gap: 414px;
  }
  
  .who-we_intro{
    width: 100%;
    height: auto;
    float: right;
  }
  
  .who-we_left{
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: 32px;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  .who-we_right{
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 50px;
  }
  
  .who-we_right-title{
    color: #FFF;
    font-family: Manrope;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 105%;
    letter-spacing: -1.6px;
    text-transform: uppercase;
  }
  
  .who-we_right-content{
    display: flex;
    width: 100%;
    gap: 32px;
    justify-content: space-between;
    align-items: flex-end;
    
