      width: 440px;
    }
  }
}

.get-closer{
  padding: 140px 0;
  
  .wrapper{
    display: flex;
    flex-direction: column;
    gap: 160px;
    
    h2{
      color: #FFF;
      font-family: Manrope;
      font-size: 64px;
      font-style: normal;
      font-weight: 500;
      line-height: 100%;
      letter-spacing: -3.2px;
      text-transform: uppercase;
      width: 1054px;
    }
  }
  
  .get-closer_content{
    width: 100%;
    display: flex;
    gap: 64px;
    justify-content: space-between;
    align-items: flex-end;
    
    p{
      width: 380px;
    }
  }
  
  .get-closer_video-container{
    height: 560px;
    width: 440px;
    overflow: hidden;

    .get-closer_video-wrapper{
      height: 560px;
      width: 440px;
      position: relative;
    }
    
    .get-closer_video-preview{
      width: 100%;
      height: auto;
    }
    
    .get-closer_video-play{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 60px;
      height: 60px;
      transition: .3s;
      cursor: pointer;
      
      &:hover{
        transform: translate(-50%, -50%) scale(1.1);
      }
    }
  }
}

.partners{
  padding-bottom: 140px;
  
