    color: #FFF;
    font-family: Manrope;
    font-size: 185px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: -9.25px;
    text-transform: uppercase;
    
    span{
      color: #FFF;
      font-family: Gallient;
      font-size: 165px;
      font-style: normal;
      font-weight: 400;
