.button{
  color: #FFF;
  font-family: Manrope;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 125%;
  letter-spacing: -0.8px;
  padding-bottom: 4px;
  position: relative;
  display: block;
  height: fit-content;
  width: fit-content;
  text-transform: uppercase;

  &:not(.no-underline):hover::before {
    transform-origin: 0% 50%;
    transform: scale3d(0, 1, 1);
  }

  &:not(.no-underline)::before {
    content:"";
    position: absolute;
    width: 100%;
    height: 1.5px;
    bottom: 0;
    right: 0;
    pointer-events: none;
    transform-origin: 100% 50%;
    transform: scale3d(1, 1, 1);
    transition: transform 0.3s;
    background: white;
  }
}

